<template>
	<view class="content-warpe">
		<!-- 地址 -->
		<view class="address-section" @click="choseAddress">
			<view class="order-content">
				<view class="cen">
					<view class="top" style="display: flex;">
						<view class="iconfont icon-dianpu">
						</view>
						<text class="mobile">京东家电</text>
					</view>
					<text class="address">黄河路北  东大街</text>
				</view>
				<view class="iconfont icon-youjiantou"></view>
			</view>
		</view>

		<view class="goods-section">
			<!-- 商品列表 -->

			<view class="yt-list-cell b-b"><view class="iconfont icon-4"></view>   天猫</view>
			<view class="g-item" v-for="(v, k) in dataList" :key="k">
				<image :src="v.img" class="product-img"></image>
				<view class="right">
					<text class="title clamp">{{ v.name }}</text>
					<view style="margin: 5px 0;">
						<text class="spec" v-if="v.sku">规格: {{ v.sku }}</text>
					</view>
					
					<view class="price-box">
						<text class="price">￥{{ v.price }}</text>
						<text class="number">x {{ v.count }}</text>
					</view>
				</view>
			</view>
		</view>

		<!-- 金额明细 -->
		<view class="yt-list">
			<view class="yt-list-cell b-b">
				<text class="cell-tip">数量</text>
				<view class="cell-tit clamp">{{ totalCount }}</view>
			</view>
			<view class="yt-list-cell b-b">
				<text class="cell-tip">单价</text>
				<view class="cell-tit icon-swiper"><view class="clamp">￥{{ freight }}</view></view>
			</view>
			<view class="yt-list-cell b-b">
				<text class="cell-tip"> 共计</text>
				<view class="cell-tit clamp">￥{{ endPrice }}</view>
			</view>
		</view>
		<!-- 备注 -->

		<view class="remark-con">
			<view class="remark-con-tit">留言</view>
			<editor class="remark-con-info" placeholder="请输入你的特殊需求" />
		</view>
		<!-- 底部 -->
		<view class="footer">
			<view class="price-content">
				<text>合计</text>
				<text class="price-tip" v-if="endPrice != 0">￥</text>
				<text class="price">{{ endPrice }}</text>
			</view>
			<text class="submit" @click="submit" hover-class="btn-hover">提交订单</text>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			
			topcheck:false,
			desc: '', //备注
			addressData: {
				name: '刘晓晓',
				phone: '18666666666',
				address: '贵族皇仕牛排(东城店)',
				default: 1,
				id: '4554564tretuji'
			},
			freight: 10,
			addrId: '56658',
			dataList: [],
			type: '',
			content: ''
		};
	},
	computed: {
		shopPrice() {
			let price = 0;
			this.dataList.forEach(item => {
				price += (item.price - 0) * 100 * (item.count - 0);
			});
			return this.$api.parsePrice(price / 100);
		},
		endPrice() {
			if (this.freight == '') {
				this.freight = 0;
			}
			return this.shopPrice - 0 + (this.freight - 0);
		},
		totalCount() {
			let num = 0;
			this.dataList.forEach(item => {
				num += item.count - 0;
			});
			return num;
		}
	},
	onLoad(options) {
		this.dataList=JSON.parse(options.good)
	},

	methods: {
		submit() {
			this.addrId == '' ? this.$api.tip('请选择收货地址!') : this.getOrderId();
		},
		//  默认地址
		getDefault() {
			this._reqw
				.ipost(
					{
						uid: getApp().globalData.uid
					},
					'findDefaultAddr'
				)
				.then(res => {
					console.log(res);
					res.result == 0 ? ((this.addrId = res.addrId), (this.addressData = res)) : this.$api.tip(res.resultNote);
				})
				.catch(err => {});
		},
		choseAddress() {
			this.$api.navTo(`/pagesA/mine/address?source=1`);
		},
		// payType  0-直接购买，1-购物车结算
		getOrderId() {
			this.$api.navTo('/pagesD/order/paySuccess')
			let productList = [];
			console.log(this.dataList);
			this.dataList.forEach(item => {
				this.payType == 0
					? productList.push({
							skuId: item.skuId,
							qty: item.count,
							productId: item.id
					  })
					: productList.push({
							qty: item.count,
							cartId: item.id,
							productId: item.productId
					  });
			});
			console.log(productList);
			let parmas = {
				uid: getApp().globalData.uid,
				type: this.payType,
				addrId: this.addrId,
				couponId: this.complinId,
				totalPrice: this.shopPrice,
				freight: this.freight,
				taxes: this.taxes,
				payMoney: this.endPrice,
				point: this.totalPoint,
				productList: productList
			};
			console.log(parmas);
			// this._reqw
			// 	.ipost(parmas, 'saveOrder')
			// 	.then(res => {
			// 		res.result == 0 ? this.wxPay(res.orderNum) : this.$api.tip(res.resultNote);
			// 	})
			// 	.catch(err => {});
		},
		wxPay(id) {
			let parmas = {
				orderNum: id,
				uid: getApp().globalData.uid,
				type: this.payType
			};
			this._reqw
				.ipost(parmas, 'wxpay')
				.then(res => {
					res.result == 0 ? this.$api.PayBywx(res.body) : this.$api.tip(res.resultNote);
				})
				.catch(err => {});
		}
	}
};
</script>

<style lang="scss">
	.icon-dianpu{
		font-size: 32upx;
	}
	.top{
		display: flex;
		align-items: center;
	}
	.icon-content{
		display: flex;
		align-items: center;
	}
	page{
		background-color:#F8F8F8 ;
	}
	.content-warpe{
		width: 95%;
		margin: 0 auto;
	}
.remark-con {
	padding: 20upx;
	margin-top: 20upx;
	background-color: #ffffff;
	color: #333333;
}
.remark-con-tit {
	margin: 20upx 0;
}

.remark-con-info {
	padding: 20upx;
	color: #333333;
}
page {
	background: $page-color-base;
	padding-bottom: 100upx;
}

.tishiimg {
	width: 14px;
	height: 14px;
	margin-left: 5px;
}
.mobile{
	font-weight: bold;
}

.icon-swiper {
	display: flex;
	align-items: center;
}

.address-section {
	height: 180upx;
	box-sizing: border-box;
	display: flex;
	align-items: center;
	justify-content: space-between;
	background: #fff;
	position: relative;
    margin-top: 20upx;
	.order-content {
		width: 100%;
		padding:0 40upx;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		
		
	}
	.cen {
		display: flex;
		flex-direction: column;
		flex: 1;
		font-size: 28upx;
		color: $font-color-dark;
	}

	.name {
		font-size: 34upx;
		margin-right: 24upx;
	}

	.address {
		margin-top: 16upx;
		margin-right: 20upx;
		color: $font-color-light;
	}
}

.goods-section {
	margin-top: 16upx;
	background: #fff;
	padding-bottom: 1px;

	.g-header {
		display: flex;
		align-items: center;
		height: 84upx;
		padding: 0 30upx;
		position: relative;
	}

	.name {
		font-size: 30upx;
		color: $font-color-base;
		margin-left: 24upx;
	}

	.g-item {
		display: flex;
		padding: 15px;

		.product-img {
			flex-shrink: 0;
			display: block;
			width: 90px;
			height: 90px;
			border-radius: 4px;
		}

		.right {
			flex: 1;
			display: flex;
			flex-direction: column;
			padding-left: 24upx;
			overflow: hidden;
		}

		.title {
			font-size: 30upx;
			color: $font-color-dark;
			word-break: break-all;
			display: -webkit-box;
			overflow: hidden;
			line-height: 1.5;
			text-overflow: ellipsis;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 2;
			font-weight: bold;
			color: #111111;
		}

		.spec {
			font-size: 26upx;
			color: $font-color-light;
			background-color: #edecf1;
			padding: 0 10px;
		}

		.price-box {
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: 32upx;
			color: $font-color-dark;
			margin-topl: 20upx;

			.price {
				font-size: 15px;
				font-weight: bold;
				color: #fe2c2c;
			}

			.number {
				font-size: 13px;
				color: #111111;
				margin-left: 20upx;
			}
		}

		.step-box {
			position: relative;
		}
	}
}

.yt-list {
	// margin-top: 16upx;
	background: #fff;
}

.yt-list-cell {
	display: flex;
	align-items: center;
	padding: 10upx 30upx 10upx 40upx;
	line-height: 70upx;
	position: relative;

	&.cell-hover {
		background: #fafafa;
	}

	&.b-b:after {
		left: 30upx;
	}

	.cell-icon {
		height: 32upx;
		width: 32upx;
		font-size: 22upx;
		color: #fff;
		text-align: center;
		line-height: 32upx;
		background: #f85e52;
		border-radius: 4upx;
		margin-right: 12upx;

		&.hb {
			background: #ffaa0e;
		}

		&.lpk {
			background: #3ab54a;
		}
	}

	.cell-more {
		align-self: center;
		font-size: 24upx;
		color: $font-color-light;
		margin-left: 8upx;
		margin-right: -10upx;
	}

	.cell-tit {
		font-size: 26upx;
		color: $font-color-light;
		margin-right: 10upx;
	}

	.cell-tip {
		flex: 1;
		font-size: 26upx;
		color: $font-color-dark;

		&.disabled {
			color: $font-color-light;
		}

		&.active {
			color: $base-color;
		}

		&.red {
			color: $base-color;
		}
	}

	&.desc-cell {
		.cell-tit {
			max-width: 90upx;
		}
	}

	
}

.footer {
	position: fixed;
	left: 0;
	bottom: 0;
	z-index: 995;
	display: flex;
	align-items: center;
	width: 100%;
	height: 90upx;
	justify-content: space-between;
	font-size: 30upx;
	background-color: #fff;
	z-index: 998;
	color: $font-color-base;
	box-shadow: 0 -1px 5px rgba(0, 0, 0, 0.1);
	padding: 0 10px;
	box-sizing: border-box;

	.price-content {
		padding-left: 30upx;
	}

	.price-tip {
		color: $base-color;
		margin-left: 8upx;
	}

	.price {
		font-size: 36upx;
		color: $base-color;
	}

	.submit {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 3px 30upx;
		margin-right: 8upx;
		color: #fff;
		border-radius: 40upx;
		font-size: 28upx;
		background-color: $page-color-light;
	}
}
</style>
